body {
  text-align: center;
  background-color: #13151a;
  color: white;
  height: 100%;
}

footer {
  font-size: calc(2px + 1vmin);
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: left;
  background-color: #2b303b;
}

.nav-link {
  color: white;
  padding: .5% 1%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.nav-link:hover {
  color: rgb(247, 71, 71);
}

#Name {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.page-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-bottom: 0.5px solid white;
}
